<template>
  <div class="content-records">
    <h1>
      内容记录查询
      <div class="header-buttons">
        <el-button type="default" size="small" icon="el-icon-back" @click="goBack">返回管理面板</el-button>
        <el-button type="primary" size="small" icon="el-icon-s-home" @click="goHome">返回主页</el-button>
      </div>
    </h1>

    <!-- 搜索条件 -->
    <el-card class="filter-card">
      <el-form :inline="true" :model="filterForm" class="filter-form" size="small">
        <el-form-item label="时间范围">
          <el-date-picker
            v-model="filterForm.dateRange"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="部门">
          <el-cascader
            v-model="filterForm.department"
            :options="departmentOptions"
            :props="{ checkStrictly: true }"
            clearable
            placeholder="请选择部门">
          </el-cascader>
        </el-form-item>
        <el-form-item label="用户">
          <el-select
            v-model="filterForm.userId"
            filterable
            remote
            reserve-keyword
            placeholder="请输入用户名"
            :remote-method="remoteSearchUsers"
            :loading="userSearchLoading"
            clearable>
            <el-option
              v-for="user in userOptions"
              :key="user.id"
              :label="user.name"
              :value="user.id">
              <span>{{ user.name }}</span>
              <span class="user-email">{{ user.email }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="内容搜索">
          <el-input
            v-model="filterForm.keyword"
            placeholder="请输入关键词"
            clearable
            @keyup.enter.native="handleSearch">
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
          <el-button icon="el-icon-refresh" @click="resetFilter">重置</el-button>
          <el-button type="success" icon="el-icon-download" @click="exportRecords">导出</el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <!-- 内容记录表格 -->
    <el-card class="table-card">
      <div slot="header" class="card-header">
        <span>对话记录列表</span>
        <el-radio-group v-model="viewMode" size="small">
          <el-radio-button label="table">表格视图</el-radio-button>
          <el-radio-button label="detail">详细视图</el-radio-button>
        </el-radio-group>
      </div>

      <!-- 表格视图 -->
      <template v-if="viewMode === 'table'">
        <el-table
          :data="recordsList"
          style="width: 100%"
          v-loading="loading">
          <el-table-column prop="timestamp" label="时间" width="180" :formatter="formatDate"></el-table-column>
          <el-table-column prop="department" label="部门" width="120"></el-table-column>
          <el-table-column prop="username" label="用户" width="120"></el-table-column>
          <el-table-column prop="question" label="问题" show-overflow-tooltip></el-table-column>
          <el-table-column label="操作" width="100" fixed="right">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="text"
                @click="viewDetail(scope.row)">
                查看详情
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>

      <!-- 详细视图 -->
      <template v-else>
        <div class="detail-list">
          <div v-for="record in recordsList" :key="record.id" class="detail-item">
            <div class="detail-header">
              <div class="detail-info">
                <span class="time">{{ formatDate(null, null, record.timestamp) }}</span>
                <el-tag size="small" type="info">{{ record.department }}</el-tag>
                <span class="username">{{ record.username }}</span>
              </div>
              <el-button type="text" @click="copyContent(record)">复制内容</el-button>
            </div>
            <div class="conversation">
              <div class="question">
                <div class="avatar user">用户</div>
                <div class="content">{{ record.question }}</div>
              </div>
              <div class="answer">
                <div class="avatar assistant">AI</div>
                <div class="content">{{ record.answer }}</div>
              </div>
            </div>
          </div>
        </div>
      </template>

      <!-- 分页 -->
      <div class="pagination-container">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </el-card>

    <!-- 详情对话框 -->
    <el-dialog
      title="对话详情"
      :visible.sync="dialogVisible"
      width="600px"
      custom-class="detail-dialog">
      <template v-if="currentRecord">
        <div class="dialog-header-info">
          <div class="info-item">
            <span class="label">时间：</span>
            <span>{{ formatDate(null, null, currentRecord.timestamp) }}</span>
          </div>
          <div class="info-item">
            <span class="label">部门：</span>
            <span>{{ currentRecord.department }}</span>
          </div>
          <div class="info-item">
            <span class="label">用户：</span>
            <span>{{ currentRecord.username }}</span>
          </div>
        </div>
        <div class="dialog-conversation">
          <div class="dialog-message user">
            <div class="message-header">问题：</div>
            <div class="message-content">{{ currentRecord.question }}</div>
          </div>
          <div class="dialog-message assistant">
            <div class="message-header">回答：</div>
            <div class="message-content">{{ currentRecord.answer }}</div>
          </div>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'ContentRecords',
  data() {
    return {
      loading: false,
      userSearchLoading: false,
      viewMode: 'table',
      currentPage: 1,
      pageSize: 10,
      total: 0,
      dialogVisible: false,
      currentRecord: null,
      filterForm: {
        dateRange: [],
        department: [],
        userId: '',
        keyword: ''
      },
      departmentOptions: [
        {
          value: 1,
          label: '研发部',
          children: [
            {
              value: 4,
              label: '前端组'
            },
            {
              value: 5,
              label: '后端组'
            }
          ]
        },
        {
          value: 2,
          label: '产品部'
        },
        {
          value: 3,
          label: '运营部'
        }
      ],
      userOptions: [],
      recordsList: [
        {
          id: 1,
          timestamp: '2024-03-20 10:30:00',
          department: '研发部-前端组',
          username: '张三',
          question: '如何优化React组件的性能？',
          answer: '优化React组件性能可以从以下几个方面入手：1. 使用React.memo()避免不必要的重渲染 2. 合理使用useMemo和useCallback 3. 使用虚拟列表处理长列表 4. 避免内联对象和函数...'
        },
        {
          id: 2,
          timestamp: '2024-03-20 11:15:00',
          department: '研发部-后端组',
          username: '李四',
          question: '如何处理Node.js中的内存泄漏问题？',
          answer: '处理Node.js内存泄漏可以：1. 使用heapdump分析内存快照 2. 避免闭包造成的引用无法释放 3. 及时清理定时器和事件监听器 4. 使用WeakMap/WeakSet处理对象引用...'
        }
      ],
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      }
    }
  },
  methods: {
    goBack() {
      if (this.$route.path !== '/admin') {
        this.$router.push('/admin');
      }
    },
    goHome() {
      if (this.$route.path !== '/') {
        this.$router.push('/');
      }
    },
    handleSearch() {
      this.currentPage = 1;
      this.fetchRecords();
    },
    resetFilter() {
      this.filterForm = {
        dateRange: [],
        department: [],
        userId: '',
        keyword: ''
      };
      this.handleSearch();
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.fetchRecords();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.fetchRecords();
    },
    formatDate(row, column, cellValue) {
      return cellValue;
    },
    viewDetail(record) {
      this.currentRecord = record;
      this.dialogVisible = true;
    },
    copyContent(record) {
      const content = `问题：${record.question}\n\n回答：${record.answer}`;
      navigator.clipboard.writeText(content).then(() => {
        this.$message.success('内容已复制到剪贴板');
      }).catch(() => {
        this.$message.error('复制失败，请手动复制');
      });
    },
    async remoteSearchUsers(query) {
      if (query) {
        this.userSearchLoading = true;
        // 模拟API调用
        setTimeout(() => {
          this.userOptions = [
            { id: 1, name: '张三', email: 'zhangsan@example.com' },
            { id: 2, name: '李四', email: 'lisi@example.com' }
          ].filter(user => user.name.includes(query));
          this.userSearchLoading = false;
        }, 500);
      }
    },
    fetchRecords() {
      this.loading = true;
      // 模拟API调用
      setTimeout(() => {
        this.total = this.recordsList.length;
        this.loading = false;
      }, 500);
    },
    exportRecords() {
      this.$message({
        message: '导出功能开发中...',
        type: 'info'
      });
    }
  },
  created() {
    this.fetchRecords();
  }
}
</script>

